.header {
  background: black;
}

footer {
  background: black;
  height: 5em;
  border-top: 1em #00c6b5 solid;
}

.card {
  background: #fff;
  border-radius: 0.25em;
  border: 1px solid #e7eae9;
  margin: 0.5em 0;
}

.arrow {
  font-size: 2em;
  cursor: pointer;
}

.arrow__down::before {
  content: "\2B07";
}

.arrow__up::before {
  content: "\2B06";
}

.store {
  border-bottom: 1px solid #e7eae9;
  background: #fff;
}

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: sans-serif;
  background: #f1f5f6;
  color: #474a49;
}

* {
  border-collapse: collapse;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list li {
  margin-bottom: 0.25em;
}

.flex {
  display: flex;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
          column-gap: 1em;
}

.stretch {
  flex: 1 1;
}

.container {
  padding: 1em;
  max-width: 1000px;
  margin: 0 auto;
}

.p {
  padding: 1em;
}

.pt {
  padding-top: 1em;
}

.py {
  padding: 1em 0;
}

.mb {
  margin-bottom: 1em;
}

.label {
  background: red;
  color: #fff;
  border-radius: 0.25em;
  padding: 0.15em;
  margin-left: 0.5em;
}

.stars {
  color: #00c6b5;
}

h1,
h2,
h3,
h4 {
  padding: 0;
  margin: 0;
}

h1 {
  font-size: 3em;
}

h3 {
  font-weight: normal;
}

.bt {
  border-top: #e7eae9 1px solid;
}

